.call-us {
	display: inline-block;
	min-width: rem(298);
	width: 45%;
	height: rem(50);
	border-radius: rem(25);
	margin-right: rem(20);
	border: solid rem(2.1) $primary-color;
	overflow: hidden;
	transition: background-color  0.2s, box-shadow 0.2s;
	vertical-align: middle;
	&:hover, &:active, &:focus {
		background-color: lighten($blue-color, 3%);
		box-shadow: -1px 1px 0px 0px $primary-color;
	}
	@include tablet {
		min-width: 14.625rem;
	}
	@include mobile {
		margin-right: rem(7);
		padding: 0.04rem 0.3rem 0.04rem 0.8rem;
		min-width: 11rem;
		height: 2.7rem;
	}
}

.call-us__link {
	display: flex;
	width: 100%;
	height: 100%;
	color: $primary-color;
	font-size: rem(32.5);
	align-items: center;
	font-weight: 300;
	font-style: normal;
	justify-content: center;
	transition: color 0.2s, background-color 0.2s;
	letter-spacing: 1.4px;
	padding: rem(2) rem(19);
	background-color: transparent;
	@include custom {
		font-size: rem(24);
	}
	& span {
		margin: 0 auto;
	}
	@include tablet {
		font-size: rem(24);
	}
	@include mobile {
		font-size: 1.15rem;
		padding: rem(1);
	}
}

.phone-svg {
	@include size(rem(28));
	fill: $primary-color;
	stroke: $primary-color;
	transition: fill 0.2s;
}

.translate-thailand, .translate-english {
	@include size(rem(40));
	display: inline-block;
	border-radius: 50%;
	overflow: hidden;
	vertical-align: middle;
	transition: box-shadow 0.2s;
	&:hover, &:focus, &:active {
		box-shadow: 0px 0px 5px 3px $primary-color;
	}
}

.translate-thailand {
	margin-right: rem(5);
}

.translate-thailand__link {
	display: block;
	@include size(100%);
	background: url('../images/icons/thailand.svg') center center/cover no-repeat;
}

.translate-english__link {
	display: block;
	@include size(100%);
	background: url('../images/icons/unitedkingdom.svg') center center/cover no-repeat;
}


