.corporate {
	min-height: rem(495);
	padding-top: 2.4rem;
	background: rgb(203,73,119);
	background: radial-gradient(circle at 30% 50%, #f5b2ca 0%, #c73f6f 41%);
	background: -webkit-radial-gradient(circle at 30% 50%, #f5b2ca 0%, #c73f6f 41%);
	background: -moz-radial-gradient(circle at 30% 50%, #f5b2ca 0%, #c73f6f 41%);
	@include laptop {
		background: rgb(203,73,119);
		background: radial-gradient(circle at 30% 63%, #f5b2ca 0%, #c73f6f 55%);
		background: -webkit-radial-gradient(circle at 30% 63%, #f5b2ca 0%, #c73f6f 55%);
		background: -moz-radial-gradient(circle at 30% 63%, #f5b2ca 0%, #c73f6f 55%);
	}
	@include tablet {
		background: rgb(203,73,119);
		background: radial-gradient(circle at 47% 74%, #f5b2ca 0%, #c73f6f 62%);
		background: -moz-radial-gradient(circle at 47% 74%, #f5b2ca 0%, #c73f6f 62%);
		background: -webkit-radial-gradient(circle at 47% 74%, #f5b2ca 0%, #c73f6f 62%);
	}
}

.corporate__col-one {
	width: 50%;
	height: 100%;
	position: relative;
	min-height: rem(495);
	@include custom {
		min-height: 34rem;
	}
	@include laptop {
		min-height: 40rem;
	}
	@include tablet {
		width: 100%;
		order: 2;
		min-height: 34.9375rem;
	}
	@include tablet_610 {
		min-height: 27.5rem;
	}
	@include mobile {
		min-height: 26.5rem;
	}
	@include s-mobile {
		min-height: 21.5rem;
	}
}

.corporate__col-two {
	width: 50%;
	height: 100%;
	min-height: rem(495);
	@include tablet {
		width: 100%;
		order: 1;
		min-height: 23.9375rem;
	}
}

.corporate__img-wr {
	position: absolute;
	bottom: 0;
	width: 70%;
	height: auto;
	left: 50%;
	transform: translateX(-50%);
	z-index: 5;
    display: flex;
    align-items: flex-end;
    @include custom {
    	width: 64%;
    }
    @include desktop {
    	width: 80%;
    }
    @include laptop {
    	width: 100%;
    }
}

.corporate__img {
	max-width: 100%;
	height: auto;
}

.corporate__text {
	font-size: rem(24);
	font-weight: 300;
	text-align: left;
	color: $primary-color;
	margin-bottom: rem(51);
	& p {
		margin-bottom: rem(10);
	}
	@include mobile {
		text-align: center;
	}
}

.corporate__order {
	display: inline-block;
	text-align: center;
	width: 100%;
	@include mobile {
		width: 95%;
		margin-right: auto;
		margin-left: auto;
	}
}

.corporate__button {
	width: rem(501);
	height: rem(52);
	border-radius: rem(26);
	background-color: transparent;
	border: solid rem(1.5) $primary-color;
	outline: none;
	overflow: hidden;
	text-align: center;
	box-shadow: none;
	transition: background-color 0.25s, box-shadow 0.25s;
	&:hover, &:focus, &:active {
		cursor: pointer;
		background-color: $pink-color;
		box-shadow: -1px 2px 0px 0px $primary-color;
	}
	& span {
		overflow: hidden;
		white-space: nowrap;
		text-overflow: ellipsis;
		vertical-align: middle;
		font-size: rem(24);
		font-weight: 300;
		font-style: normal;
		line-height: rem(24);
		text-align: center;
		color: $primary-color;
	}
	@include desktop {
		width: 82%;
	}
	@include laptop {
		width: 82%;
	}
	@include mobile {
		width: 100%;
	}
}


