.parsley-errors-list {
	font-size: rem(12);
    position: absolute;
   	z-index: 10;
    left: 30%;
    margin-top: rem(2);
    color: #ffffff;
    background: rgb(200, 53, 53);
    box-shadow: -2px 2px 2px 0px rgba(211, 77, 77, 0.47);
    padding: rem(4) rem(10);
    border-radius: 1px;
    line-height: rem(12);
    pointer-events: none;
    &:before {
    	content: '';
    	width: 0;
    	height: 0;
		top: -5px;
		position: absolute;
		left: 6px;
		border-left: 5px solid rgba(0, 0, 0, 0);
		border-right: 5px solid rgba(0, 0, 0, 0);
		border-bottom: 5px solid #c83535;
    }
}

form input[type='text'].parsley-error, form input[type='tel'].parsley-error, form textarea.parsley-error {
    border: 1px solid #fa4531;
}
.parsley-success + .parsley-errors-list {
	display: none;
}

.form-row-vh {
    border: 0;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
    left: -9999px;
}


