.in-furniture {
	min-height: rem(820);
	background-color: $bg-color;
	padding: rem(50) 0;
}

.in-furniture__wrapper {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	width: 100%;
	height: 100%;
}

.in-furniture__col-one {
	width: 50%;
	padding-right: 1.5%;
	@include tablet {
		width: 100%;
		padding-right: 0;
    	margin-bottom: 4rem;
	}
}
.in-furniture__col-two {
	width: 50%;
	padding-left: 1.5%;
	@include tablet {
		width: 100%;
		padding-left: 0;
	}
}

.problem-card__content {
	display: flex;
}

.problem-card__main-img-wr {
	@include size(rem(330));
	border-radius: 50%;
	background-size: cover;
	background-position: center right;
	margin-top: rem(10);
	margin-right: 4%;
	position: relative;
	flex-shrink: 0;
	&:before {
		content: '';
		display: block;
		@include size(rem(17), rem(18));
		border-radius: 50%;
		background-color: $pink-color;
		position: absolute;
		right: rem(30);
		top: 50%;
		transform: translateY(-50%);
		z-index: 2;
	}
	&:after {
		content: '';
		display: block;
		@include size(rem(200), rem(2));
		background-color: $pink-color;
		position: absolute;
		right: rem(30);
		top: 50%;
		transform: translate(100%, -50%);
		z-index: 0;
	}
	@include desktop {
		@include size(14.2rem);
		margin-top: 3.9rem;
	}
	@include laptop {
		width: 9.4rem;
    	height: 9.4rem;
    	margin-top: 6.2rem;
	}
	@include tablet_610 {
		@include size(14.2rem);
		margin-top: 3.9rem;
	}
	@include mobile {
		width: 9.4rem;
    	height: 9.4rem;
    	margin-top: 6.2rem;
	}
}

.problem-card__arrow {
	display: block;
	@include size(rem(160));
	border: solid $pink-color;
	border-width: 0 2px 0 0;
  	padding: 2px;
	background-color: transparent;
	position: absolute;
	right: 2.6rem;
	top: 50%;
	transform: translateY(-100%) rotate(143deg);
	transform-origin: bottom right;
	z-index: 0;
	&:after {
		content: '';
		display: block;
		@include size(rem(160));
		border: solid $pink-color;
		border-width: 0 0 2px 0;
  		padding: 2px;
		background-color: transparent;
		position: absolute;
		right: 0;
		top: 0;
		transform: rotate(-22.5deg);
		transform-origin: bottom right;
		z-index: 0;
	}
}

.problem-card__main-img {
	max-width: 100%;
	height: auto;
}

.problem-card__list {
	display: block;
	padding-left: rem(15);
	flex-grow: 1;
}

.problem-card__item {
	height: rem(78);
	border-radius: rem(30);
	background-color: $pink-color;
	position: relative;
	font-size: rem(24);
	font-weight: 300;
	line-height: 1;
	text-align: center;
	color: $primary-color;
	padding: rem(5) rem(13) rem(5) rem(74);
	margin-bottom: rem(42);
	display: flex;
	align-items: center;
	justify-content: center;
	&:before {
		content: '';
		display: block;
		position: absolute;
		top: 50%;
		left: 0;
		@include size(rem(75));
		overflow: hidden;
		border-radius: 50%;
		border: solid rem(4) $pink-color;
		background-repeat: no-repeat;
		background-size: cover;
		background-position: center center;
		transform: translate(-9%, -50%);
		z-index: 6;
	}
	&:after {
		content: '';
		display: block;
		position: absolute;
		top: 50%;
		left: 0;
		@include size(rem(75));
		overflow: hidden;
		border-radius: 50%;
		border: solid rem(4) $pink-color;
		background-color: rgba(0,0,0, 0.065);
		transform: translate(-9%, -50%);
		z-index: 7;
	}
	&.bg_one:before {
		background-image: url('../images/decor/bacteria_one.jpg');
	}
	&.bg_two:before {
		background-image: url('../images/decor/bacteria_two.jpg');
	}
	&.bg_three:before {
		background-image: url('../images/decor/bacteria_three.jpg');
	}
	&:last-child {
		margin-bottom: 0;
	}
}

.problem-description {
	border-radius: rem(30);
	background-color: $primary-color;
	padding: rem(30) rem(20) rem(50) rem(25);
	line-height: 1.325;
}

.problem-description__text {
	font-size: rem(24);
	font-weight: 300;
	color: $text-color;
	text-align: left;
	margin-bottom: rem(20);
}

.problem-description__marker {
	font-size: rem(30);
	font-weight: 900;
	text-align: center;
	color: $pink-color;
}

.in-furniture__order {
	margin-top: rem(72);
	margin: rem(72) auto 0 auto;
	@include mobile {
		width: 95%;
		margin-right: auto;
		margin-left: auto;
	}
}

.in-furniture__button {
	width: rem(501);
	height: rem(52);
	border-radius: rem(26);
	background-color: transparent;
	border: solid rem(1.5) $pink-color;
	outline: none;
	overflow: hidden;
	text-align: center;
	transition: background-color 0.2s, box-shadow 0.3s;
	&:hover, &:focus, &:active {
		cursor: pointer;
		background-color: $primary-color;
		box-shadow: -1px 2px 0px 0px $pink-color;
	}
	& span {
		overflow: hidden;
		white-space: nowrap;
		text-overflow: ellipsis;
		vertical-align: middle;
		font-size: rem(24);
		font-weight: 300;
		font-style: normal;
		line-height: rem(24);
		text-align: center;
		color: $pink-color;
	}
	@include mobile {
		width: 100%;
	}
}


