.hero {
	height: 100vh;
	min-height: 630px;
	background: url('../images/decor/hero_bg.jpg') center bottom/cover no-repeat;
	display: flex;
	flex-direction: column;
	position: relative;
	@include tablet {
		min-height: 100vh;
	}
}

.header {
	width: 100%;
	min-height: rem(74);
	padding: rem(23) 0;
	background-color: $blue-color;
	flex-shrink: 0;
}

.hero-main {
	width: 100%;
	flex-grow: 1;
}

.main-title-wr {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	text-align: center;
	min-width: 60%;
}

.hero__next {
	position: absolute;
	top: 100%;
	left: 50%;
	transform: translate(-50%, -135%);
	display: flex;
	justify-content: center;
	align-items: center;
	width: 3.9rem;
	height: 3.9rem;
	transition: transform 0.2s;
	transform-origin: top center;
	&:hover {
		transform: translate(-50%, -145%);
	}
}

.scroll-svg {
	width: 3.9rem;
	height: 3.9rem;
	stroke-width: 2px;
	fill: $primary-color;
	stroke: $primary-color;
	opacity: 1;
}


