.service {
	min-height: rem(1587);
	background-color: $bg-color;
	padding: rem(45) 0;
	@include tablet {
		padding: 2rem 0;
	}
	@include tablet_610 {
		padding: 1rem 0;
	}
}

.service__list {
	width: 100%;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	align-items: baseline;
	overflow: hidden;
}

.service__item {
	width: 32.5%;
	min-height: rem(441);
	border-radius: 30px;
	background-color: $grey-color;
	overflow: hidden;
	padding-bottom: rem(30);
	margin-bottom: rem(25);
	@include tablet {
		width: 48.9%;
	}
	@include mobile {
		width: 85%;
    	margin-left: auto;
    	margin-right: auto;
	}
}

.service__img-wr {
	width: 100%;
	height: rem(258);
	background-size: cover;
	background-position: center center;
	margin-bottom: 0.8rem;
	position: relative;
	&:after {
		content: '';
		display: block;
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		z-index: 1;
		background-color: rgba(0,0,0, 0.02);
	}
}

.service__img {
	max-width: 100%;
	height: auto;
}

.service__content {
	width: 100%;
	padding: 0 rem(10) rem(23) rem(10);
}

.service__title {
	text-align: center;
	font-size: rem(23);
	font-weight: 900;
	color: $dark-blue-color;
	margin-bottom: rem(10);
}

.service__description {
	width: 100%;
	text-align: left;
}

.service__time, .service__price {
	width: 49.2%;
	display: inline-block;
	vertical-align: middle;
	@include laptop {
		width: 49.12%;
	}
	@include tablet {
		width: 49.12%;
	}
}

.service__time-icon {
	@include size(rem(32), rem(35));
	margin-right: rem(25);
	display: inline-block;
	vertical-align: middle;
	@include laptop {
		margin-right: 0.15rem;
	}
	@include tablet {
		margin-right: 0.15rem;
	}
}

.stopwatch_service-svg {
	fill: $pink-color;
	stroke: $pink-color;
	@include size(rem(32), rem(35));
}

.service__time-text, .service__price-text {
	font-size: rem(24);
	font-weight: 300;
	text-align: left;
	color: $pink-color;
	display: inline-block;
	vertical-align: middle;
}

.service__price-icon {
	@include size(rem(33), rem(35));
	margin-right: rem(25);
	display: inline-block;
	vertical-align: middle;
	@include laptop {
		margin-right: 0.15rem;
	}
	@include tablet {
		margin-right: 0.15rem;
	}
}

.wallet-svg {
	@include size(rem(33), rem(35));
	fill: $pink-color;
	stroke: $pink-color;
}

.service__order {
	display: inline-block;
	text-align: center;
	width: 100%;
}

.service__button {
	width: rem(264);
	height: rem(45);
	border-radius: rem(22.5);
	background-color: $dark-blue-color;
	border: solid 1px $dark-blue-color;
	outline: none;
	overflow: hidden;
	text-align: center;
	box-shadow: none;
	transition: background-color 0.25s, box-shadow 0.25s;
	&:hover, &:focus, &:active {
		cursor: pointer;
		background-color: $primary-color;
		box-shadow: -1px 2px 0px 0px $dark-blue-color;
	}
	&:hover span, &:focus span, &:active span {
		color: $dark-blue-color;
	}
	& span {
		overflow: hidden;
		white-space: nowrap;
		text-overflow: ellipsis;
		vertical-align: middle;
		font-size: rem(24);
		font-weight: 300;
		font-style: normal;
		line-height: rem(24);
		text-align: center;
		color: $primary-color;
		transition: color 0.25s;
	}
	@include laptop {
		width: 81%;
	}
	@include tablet_610 {
		width: 81%;
	}
}


