.benefits {
	min-height: rem(688);
	background: rgb(110,203,218);
background: -webkit-linear-gradient(rgba(110,203,218,1) 0%, rgba(26,147,166,1) 100%);
background: -o-linear-gradient(rgba(110,203,218,1) 0%, rgba(26,147,166,1) 100%);
background: linear-gradient(rgba(110,203,218,1) 0%, rgba(26,147,166,1) 100%);
	padding: rem(50) 0 rem(65) 0;
}

.benefits__wrapper {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: baseline;
	width: 100%;
	height: 100%;
}

.benefits__col-one {
	width: 50%;
	padding-bottom: rem(10);
	padding-right: 1.3%;
	@include tablet {
		width: 100%;
		padding: 0;
    	margin-bottom: 4.5rem;
	}
}

.benefits-video {
	position: relative;
	//@include size(100%, auto);
	width: 100%;
	height: rem(350);
}

.benefits-video__poster {
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	background-size: cover;
	background-position: center center;
	z-index: 9997;
	transition: opacity 0.2s;
	&.play {
		opacity: 0;
		z-index: -1;
	}
	&:after {
		content: '';
		display: block;
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		z-index: 9998;
		background-color: rgba(0,0,0, 0.05);
	}
}

.benefits-video video {
	width: 100%;
	height: auto;
	max-height: 100%;
}

.benefits-video__play {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	@include size(23%, auto);
	z-index: 9999;
	will-change: transform, opacity;
	transition: opacity 0.3s;
	display: flex;
	justify-content: center;
	align-items: center;
	&.play {
		opacity: 0;
		z-index: -1;
	}
	&:hover, &:focus {
		cursor: pointer;
	}
	.play-svg:hover, .play-svg:focus {
		opacity: 0.81;
	}
}

.play-svg {
	@include size(rem(133));
	fill: $primary-color;
	stroke: $primary-color;
	opacity: 0.61;
	transition: opacity 0.3s;
}

.benefits__col-two {
	width: 50%;
	padding: 0 0 rem(10) 1.3%;
	@include tablet {
		width: 100%;
		padding: 0;
    	margin-bottom: 1rem;
	}
}

.benefits__list {
	display: flex;
	flex-wrap: wrap;
	align-items: baseline;
	justify-content: space-between;
	padding-left: rem(10);
}

.benefits__item {
	width: 30%;
	color: $primary-color;
	overflow: hidden;
	margin-bottom: rem(33);
}

.benefits__icon {
	display: flex;
	justify-content: center;
	align-items: center;
	overflow: hidden;
	margin-bottom: rem(21);
}

.ecology-svg {
	@include size(rem(75), rem(75));
	fill: $primary-color;
	stroke: $primary-color;
}

.heart-svg {
	@include size(rem(90), rem(75));
	fill: $primary-color;
	stroke: $primary-color;
}

.aim-svg {
	@include size(rem(75), rem(75));
	fill: $primary-color;
	stroke: $primary-color;
}

.bacteria-svg {
	@include size(rem(71), rem(75));
	fill: $primary-color;
	stroke: $primary-color;
}

.fabrics-svg {
	@include size(rem(90), rem(68));
	fill: $primary-color;
	stroke: $primary-color;
}

.stopwatch-svg {
	@include size(rem(69), rem(75));
	fill: $primary-color;
	stroke: $primary-color;
}

.benefits__content {
	color: $primary-color;
	text-align: center;
	font-size: rem(18);
}

.benefits__content-title {
	font-weight: 900;
}

.benefits__content-text {
	font-weight: 300;
}

.benefits__order {
	margin-top: rem(24);
	@include mobile {
		width: 95%;
		margin-left: auto;
		margin-right: auto;
	}
}

.benefits__button {
	width: rem(501);
	height: rem(52);
	border-radius: rem(26);
	background-color: transparent;
	border: solid rem(1.5) $primary-color;
	outline: none;
	overflow: hidden;
	text-align: center;
	box-shadow: none;
	transition: background-color 0.3s, box-shadow 0.3s;
	&:hover, &:focus, &:active {
		cursor: pointer;
		background-color: $dark-blue-color;
		box-shadow: -1px 2px 0px 0px $primary-color;
	}
	& span {
		overflow: hidden;
		white-space: nowrap;
		text-overflow: ellipsis;
		vertical-align: middle;
		font-size: rem(24);
		font-weight: 300;
		font-style: normal;
		line-height: rem(24);
		text-align: center;
		color: $primary-color;
	}
	@include mobile {
		width: 100%;
	}
}


