.gallery {
	min-height: rem(808);
	background-color: $bg-color;
	padding: rem(67) 0 rem(79) 0;
	@include laptop {
		min-height: 46rem;
	}
}

.gallery-video {
	width: 98%;
	height: 50%;
	position: relative;
	margin: 0 auto rem(34) auto;
}

.gallery-video__list {
	width: 82.5%;
	height: rem(224);
	display: flex;
	overflow: hidden;
	margin: 0 auto;
	@include custom {
		height: 15.25rem;
	}
	@include laptop {
		height: 11rem;
	}
}

.gallery-video__item {
	width: 32.2%;
	overflow: hidden;
	position: relative;
	height: rem(224);
  	border-radius: rem(30);
  	margin-right: 1.7%;
  	flex-shrink: 0;
  	@include custom {
		height: 15.25rem;
	}
	@include laptop {
		height: 11rem;
	}
	@include tablet {
		width: 49%;
	}
	@include tablet_610 {
		width: 98.3%;
	}
}

.gallery-video__poster {
	background-size: cover;
	background-position: center center;
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	z-index: 9997;
	transition: opacity 0.2s;
	&.play {
		opacity: 0;
		z-index: -1;
	}
	&:after {
		content: '';
		display: block;
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		z-index: 9998;
		background-color: rgba(0,0,0, 0.05);
	}
	&.play:after {
		z-index: -10;
	}
}

.gallery-video__play {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	@include size(35.3%, auto);
	z-index: 9999;
	will-change: opacity;
	transition: opacity 0.3s;
	display: flex;
	justify-content: center;
	align-items: center;
	&.play {
		opacity: 0;
		z-index: -1;
	}
	&:hover, &:focus {
		cursor: pointer;
	}
	.play-svg:hover, .play-svg:focus {
		opacity: 0.81;
	}
}

.gallery-video__prev {
	display: block;
	position: absolute;
	top: 50%;
	left: 0%;
    z-index: 10;
    transform: translateY(-50%);
    @include size(5.5rem, 5.8rem);
    transition: transform 0.2s;
    &:hover .arrow_prev-svg {
    	transform: scale(-1.06, 1.06);
    	transform-origin: center center;
    }
    @include laptop {
		@include size(3.5rem, 3.8rem);
	}
	@include tablet {
		@include size(3.5rem, 3.8rem);
	}
	@include mobile {
		@include size(2.5rem, 2.8rem);
	}
}

.arrow_prev-svg {
	@include size(5.5rem, 5.8rem);
	fill: $pink-color;
	stroke: $pink-color;
	transform: scale(-1, 1);
	@include laptop {
		@include size(3.5rem, 3.8rem);
	}
	@include tablet {
		@include size(3.5rem, 3.8rem);
	}
	@include mobile {
		@include size(2.5rem, 2.8rem);
	}
}

.gallery-video__next {
	display: block;
	position: absolute;
	top: 50%;
	right: rem(11);
    z-index: 10;
    transform: translateY(-50%);
    @include size(5.5rem, 5.8rem);
    transition: transform 0.2s;
    &:hover .arrow_next-svg {
    	transform: scale(1.06, 1.06);
    	transform-origin: center center;
    }
    @include laptop {
		@include size(3.5rem, 3.8rem);
	}
	@include tablet {
		@include size(3.5rem, 3.8rem);
	}
	@include mobile {
		@include size(2.5rem, 2.8rem);
	}
}

.arrow_next-svg {
	@include size(5.5rem, 5.8rem);
	fill: $pink-color;
	stroke: $pink-color;
	@include laptop {
		@include size(3.5rem, 3.8rem);
	}
	@include tablet {
		@include size(3.5rem, 3.8rem);
	}
	@include mobile {
		@include size(2.5rem, 2.8rem);
	}
}

