.overlay {
	background: rgba(0, 0, 0, 0.65);
	bottom: 0;
	cursor: default;
	left: 0;
	opacity: 0;
	position: fixed;
	right: 0;
	top: 0;
	visibility: hidden;
	z-index: 99999;
	transition: opacity 0.3s;
	will-change: opacity;
}

.popup-f {
	width: rem(400);
	min-height: rem(500);
	margin: auto;
	display: flex;
	top: 50%;
	left: 50%;
	opacity: 0;
	position: fixed;
	visibility: hidden;
	z-index: 999999;
	transform: translate(-50%, -50%);
	transition: opacity 0.2s;
	background-color: $primary-color;
	border-radius: rem(5);
	overflow: hidden;
	will-change: opacity;
	box-shadow: -1px 2px 7px 3px rgba(0,0,0,0.2);
	@include mobile {
		width: 27rem;
		min-height: 31.5rem;
	}
	@include s-mobile {
		width: 24rem;
	}
}

.popup-mes {
	width: rem(350);
	min-height: rem(200);
	margin: auto;
	display: flex;
	align-items: center;
	top: 50%;
	left: 50%;
	opacity: 0;
	position: fixed;
	visibility: hidden;
	z-index: 999999;
	transform: translate(-50%, -50%);
	transition: opacity 0.2s;
	background-color: $primary-color;
	border-radius: rem(5);
	overflow: hidden;
	will-change: opacity;
	box-shadow: -1px 2px 7px 3px rgba(0,0,0,0.2);
	@include tablet {

	}
}

.fofm__wr {
	width: 100%;
	padding: 0 rem(10);
	text-align: center;
}

.fofm-row {
	position: relative;
	margin: 0 auto rem(15) auto;
}

.popup-f form input,  .popup-f form textarea {
	width: 95%;
	height: rem(50);
	border-radius: rem(5);
	background-color: rgba(242, 160, 0, 0.31);
	border: 1px solid transparent;
    padding: rem(3) rem(10) rem(3) rem(25);
    outline: none;
    text-align: left;
    overflow: hidden;
    transition: border 0.2s;
    font-size: 1.5rem;
    line-height: rem(44);
	font-weight: 400;
	font-style: normal;
	letter-spacing: 2.2px;
	color: $placeholder-color;
	@include laptop {
		font-size: rem(20);
		@include placeholder {
			font-size: rem(20);
		}
	}
    @include placeholder {
		font-size: 1.5rem;
		font-weight: 400;
		font-style: normal;
		letter-spacing: 2.2px;
		text-align: left;
		line-height: rem(44);
		color: $placeholder-color;
		background: transparent;
	}
	&:hover, &:focus {
		border: 0.7px solid $orange-color;
		cursor: pointer;
	}
}

.popup-f form textarea {
	resize: none;
	margin-bottom: rem(30);
}

.popup-mes .window {
	width: 100%;
	height: 100%;
	padding: 0 27px 27px;
	text-align: center;
}

.popup-f .close-modal {
	position: absolute;
	top: 1.1rem;
    right: 0.7rem;
	@include size(1.8rem, 1.8rem);
	& .closemod-svg {
		width: 1.78rem;
		height: 1.78rem;
		fill: $primary-color;
		stroke: $primary-color;
	}
}
.popup-mes .close-modal {
	position: absolute;
	top: 0.7rem;
    right: 0.7rem;
	@include size(1.8rem, 1.8rem);
	& .closemod-svg {
		width: 1.78rem;
		height: 1.78rem;
		color: $orange-color;
		fill: $orange-color;
	}
}

.fofm__text {
	width: 100%;
    min-height: 2rem;
    font-size: 1.5rem;
    text-align: center;
    line-height: 1.35;
    margin-bottom: rem(20);
    color: $orange-color;
}

.insText__title {
	margin-bottom: rem(20);
    font-size: 1.3rem;
    color: $orange-color;
}

.insText__text {
	line-height: 1.67;
	font-weight: bold;
}


