.main-nav__open {
	touch-action: manipulation;
}

.main-nav__link {
	display: flex;
	align-items: center;
	color: $primary-color;
	font-family: 'open_sans';
	font-size: rem(36);
	font-weight: 300;
	&:hover .main-nav__link-text, &:active .main-nav__link-text {
		border-bottom: rem(2.1) solid rgba($primary-color, 0.89);
	}
}

.main-nav__link-text {
	display: block;
	padding: rem(9) 0;
    border-bottom: rem(2.1) solid transparent;
    transition: border-bottom 0.2s;
    @include tablet {
    	display: none;
    }
}

.menu-svg {
	@include size(rem(32), 100%);
	fill: $primary-color;
	stroke: $primary-color;
	margin-right: rem(17);
}

.fat-nav {
  top: 0;
  left: 0;
  z-index: 9999;
  position: fixed;
  display: none;
  width: 100%;
  height: 100%;
  transform: scale(0.0001);
  will-change: transform;
  background: rgba($dark-blue-color, 0.93);
  overflow-y: auto;
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
  transform-origin: center center;
}

.fat-nav__wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.fat-nav.active {
	transform: scale(1);
	transition: transform 0.3s;
}

.hamburg-menu__list {
	max-width: 55%;
	display: flex;
	flex-direction: column;
	@include mobile {
		max-width: 80%;
	}
}

.hamburg-menu__close {
	position: absolute;
	top: rem(27);
	right: rem(30);
	@include size(1.9rem, 1.9rem);
	z-index: 20;
}

.cancel-svg {
	width: 1.9rem;
	height: 1.9rem;
	fill: $primary-color;
	stroke: $primary-color;
}

.hamburg-menu__item {
	padding: 0.8rem 0;
	text-align: center;
	&:last-child {
		border-bottom: none;
	}
	&:hover .hamburg-menu__link {
		border-bottom: 1.4px solid transparent;
	}
}

.hamburg-menu__link {
	color: $primary-color;
	font-size: rem(30);
	font-weight: 300;
	text-align: center;
	line-height: 1.3;
	border-bottom: 1.4px solid $primary-color;
	transition: border-bottom 0.1s;
	@include tablet {
		font-size: rem(27);
	}
	@include s-mobile {
		font-size: rem(19);
	}
}


